export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			radioStyle1:'',
			radioStyle2:'',
			date: currentDate,
			region1: ['广西省', '梧州市', '藤县'],
			region: ['广西省', '梧州市', '藤县'],
			index6: 0,
			array6: ['博士以上', '博士', '硕士', '本科', '大专', '高中', '初中', '初中以下'],
			index7: 0,
			array7: ['未婚', '已婚'],
			index8: 0,
			array8: ['无', '有']
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		selectRaadio(a){

			if(a == 1) {
				this.radioStyle1 = 'border:1px solid #418bff'
				this.radioStyle2 = ''
			}else if(a == 2){
				this.radioStyle1 = ''
				this.radioStyle2 = 'border:1px solid #fa86ad'
			}
		},
		bindPickerChange4(e) {
			this.index6 = e.target.value;
		},
		bindPickerChange5(e) {
			this.region1 = e.target.value;
		},
		bindPickerChange6(e) {
			this.region = e.target.value;
		},
		bindPickerChange7(e) {
			this.index7 = e.target.value;
		},
		bindPickerChange8(e) {
			this.index8 = e.target.value;
		},
		bindDateChange: function(e) {
			this.date = e.target.value

		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	},
}
